<template>
  <!-- 如果是没有后代节点  -->
  <el-menu-item
    v-if="isParent"
    class="ling-menu-icon"
    :index="index">
    <template slot="title">
      <i v-if="menu.icon" :class="menu.icon"></i>
      <div v-else-if="menu.imgIcon" class="menu-icon">
        <img class="icon-img" :src="menu.imgIcon" alt="图标" />
      </div>
      <span>{{ menu.title }}</span>
    </template>
  </el-menu-item>
  <!-- 否则 -->
  <el-submenu
    v-else
    class="ling-menu-icon"
    :index="index">
    <template slot="title">
      <i v-if="menu.icon" :class="menu.icon"></i>
      <div v-else-if="menu.imgIcon" class="menu-icon">
        <img class="icon-img" :src="menu.imgIcon" alt="图标" />
      </div>
      <span>{{ menu.title }}</span>
    </template>
    <ling-menu-item
      v-for="(menu, index) in getChild"
      :key="index"
      :menu="menu"
      :index="getChildIndex(menu, index)">
    </ling-menu-item>
  </el-submenu>
</template>

<script>
const LingMenuItem = () => import('@/components/menu/LingMenuItem')
export default {
  name: 'LingMenuItem',
  components: {
    LingMenuItem
  },
  props: {
    index: {
      type: String,
      required: true
    },
    menu: {
      type: Object,
      required: true
    }
  },
  computed: {
    isParent () {
      return !this.menu.child
    },
    getChild () {
      return this.menu.child
    }
  },
  methods: {
    getChildIndex ({ path }, index) {
      return !path ? `${this.index}-${index + 1}` : path
    }
  }
}
</script>

<style lang="scss">
.ling-menu-icon {
  .fa {
    font-size: 16px;
    margin-right: 5px;
  }
  .menu-icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    line-height: 0;
    margin-right: 5px;
    font-size: 0;
    .icon-img {
      width: 18px;
      height: 18px;
      vertical-align: middle;
    }
  }
}
</style>



