<template>
  <el-menu
    :router="true"
    :default-active="activeMenu"
    @select="selectMenu">
    <ling-menu-item
      v-for="(menu, index) in menuList"
      :key="index"
      :menu="menu"
      :index="getIndex(menu, index)">
    </ling-menu-item>
  </el-menu>
</template>

<script>
import LingMenuItem from '@/components/menu/LingMenuItem'
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: 'LingMenu',
  components: {
    LingMenuItem
  },
  data () {
    return {
      menuList: [
        {
          title: '基本信息',
          icon: 'fa fa-info-circle',
          path: '/'
        },
        {
          title: '参数配置',
          icon: 'fa fa-cog',
          child: [
            {
              title: 'WIFI配置',
              icon: 'fa fa-wifi',
              path: '/wifiSetting'
            },
            {
              title: '声音配置',
              icon: 'fa fa-volume-down',
              path: '/volumeSetting'
            },
            {
              title: '温度阈值',
              icon: 'fa fa-thermometer',
              path: '/temperatureThreshold'
            },
            {
              title: '摄像头距离补偿',
              icon: 'fa fa-camera',
              path: '/cameraDistanceCompensation'
            },
            {
              title: '曝光参数',
              icon: 'fa fa-camera',
              path: '/cameraExplore'
            },
            {
              title: 'FFC补偿参数',
              icon: 'fa fa-google-wallet',
              path: '/FFCCompensation'
            },
            {
              title: 'FFC校准开关',
              icon: 'fa fa-dot-circle-o',
              path: '/FFCCalibrationSwitch'
            },
            {
              title: '人脸框的校准',
              icon: 'fa fa-user-circle',
              path: '/faceAdjusting'
            },
            {
              title: '有效区域校准',
              icon: 'fa fa-window-maximize',
              path: '/areaAdjusting'
            },
            {
              title: '本地图片查询',
              icon: 'fa fa-picture-o',
              path: '/localImages'
            },
            {
              title: '摄像头直播',
              icon: 'fa fa-file-video-o',
              path: '/cameraLive'
            }
          ]
        }
      ],
      activeMenu: ''
    }
  },
  computed: {
    ...mapGetters({
      getActiveMenu: 'getActiveMenu'
    })
  },
  watch: {
    getActiveMenu: {
      handler (val) {
        this.activeMenu = val
      },
      immediate: true
    }
  },
  methods: {
    ...mapMutations({
      changeActiveMenu: 'changeActiveMenu'
    }),
    selectMenu (val) {
      this.changeActiveMenu(val)
    },
    getIndex ({ path }, index) {
      return !path ? `${index + 1}` : path
    }
  }
}
</script>



