<template>
  <el-container class="ling-home">
    <el-header class="home-header">
      <i class="fa fa-align-justify" @click="showAside"></i>
      <span class="header-title">
        谛达诺红外测温
      </span>
      <span class="header-info">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            <i class="fa fa-user"></i>欢迎，
            <span>{{ userInfo.username }}</span>
          </span>
          <i class="el-icon-caret-bottom"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="LogOut">
              <i class="menu-icon fa fa-sign-out"></i>
              退出登录
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </span>
    </el-header>

    <el-container class="home-content">
      <transition name="el-fade-in">
        <el-aside class="home-aside" width="230px" v-show="asideShow">
          <div class="aside-bg" @click="showAside"></div>
          <ling-menu></ling-menu>
        </el-aside>
      </transition>

      <el-main class="home-body">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import LingMenu from '@/components/menu/LingMenu'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations } = createNamespacedHelpers('login')
export default {
  name: 'LingHome',
  components: {
    LingMenu
  },
  data () {
    return {
      asideShow: false
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo'
    })
  },
  methods: {
    ...mapMutations({
      removeUserInfo: 'removeUserInfo'
    }),
    LogOut () {
      this.removeUserInfo()
      this.$router.replace('/login')
    },
    showAside () {
      this.asideShow = !this.asideShow
    }
  }
}
</script>

<style lang="scss">
.ling-home {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  .home-header {
    height: 60px;
    line-height: 60px;
    color: #fff;
    background: #438eb9;
    .fa-align-justify {
      font-size: 18px;
      margin-left: -12px;
    }
    .header {
      &-title {
        font-size: 22px;
      }
      &-info {
        height: 60px;
        float: right;
        padding: 0 5px;
        cursor: pointer;
        background-color: #62a8d1;
        .el-dropdown-link {
          .fa {
            margin-right: 5px;
          }
        }
        .el-dropdown {
          color: #fff;
          .fa-user {
            margin-left: 5px;
          }
        }
      }
    }
  }
  .home-aside {
    border-right: solid 1px #e6e6e6;
    .el-menu {
      border: none;
    }
  }
  .home-body {
    position: relative;
    background: #fff;
    z-index: 1;
  }

  @media screen and (min-width: 801px) {
    .fa-align-justify {
      display: none;
    }
    .home-aside {
      display: block !important;
    }
  }
  @media screen and (max-width: 800px) {
    .home-content {
      position: relative;
    }
    .home-aside {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 10;
      width: unset !important;
      .aside-bg {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: rgba(0, 0, 0, .1);
        z-index: 1;
      }
      .el-menu {
        width: 230px;
        height: 100%;
        z-index: 5;
      }
    }
    .header-info {
      margin-right: -20px;
    }
  }
  @media screen and (max-width: 400px) {
    .header-title {
      display: none;
    }
    .header-info {
      background-color: inherit !important;
    }
  }
}
</style>



